.title {
  font-family: "FuturaPT", Arial, Helvetica, sans-serif;
  font-weight: 500;
  font-size: 22px;
  line-height: 129%;
  color: $color-181818;
  margin-bottom: 20px;

  @include mobile {
    font-size: 20px;
    margin-bottom: 15px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }

  &_sm {
    font-weight: 450;
    font-size: 16px;
  }

  &_big {
    font-size: 45px;
    font-weight: 600;

    @include mobile {
      font-size: 32px;
    }
  }
}
