* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
  box-sizing: border-box;
}

.container {
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 1040px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin: -10px;
}

.col {
  width: 100%;
  padding: 10px;

  &_4 {
    width: 33.333333%;

    @include tablet {
      width: 100%;
    }
  }

  &_8 {
    width: 66.666667%;

    @include tablet {
      width: 100%;
    }
  }
}
