body {
  font-family: "FuturaPT", Arial, Helvetica, sans-serif;
  font-weight: normal;
  font-size: 18px;
  line-height: 129%;
  // color: $color-333333;
  color: $color-7E8791;
  background: $color-F5F5F7;

  @include mobile {
    font-size: 16px;
  }
}

.layout {
  padding-top: 80px;
  padding-bottom: 80px;

  @include tablet {
    padding-top: 60px;
    padding-bottom: 40px;
  }

  &__items {
    position: relative;
    display: flex;
    flex-direction: column;
    background: $color-FFFFFF;
    box-shadow: $shadow-1;

    @include tablet {
      position: initial;
    }
  }

  &__content {
    padding: 80px 20px 50px 20px;
    max-width: 560px;
    margin-left: auto;
    margin-right: auto;

    @include tablet_sm {
      padding: 40px 20px;
    }
  }
}

.item {
  margin-bottom: 50px;

  @include tablet_sm {
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &:last-child {
    margin-bottom: 0;
  }

  > .title {
    &_sm {
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__info {
    font-size: 12px;
    color: $color-181818;
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }

    span {
      color: $color-BEAD8E;
    }
  }

  &__list {
    padding-left: 25px;

    li {
      list-style: disc;
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}

.projects {
  &__list {
    li {
      margin-bottom: 5px;

      &:last-child {
        margin-bottom: 0;
      }

      a {
        color: $color-7E8791;
      }

      span {
        &::before,
        &::after {
          color: $color-181818;
        }

        &::before {
          content: "[ ";
        }
        &::after {
          content: " ]";
        }
      }

      p {
        font-size: 14px;
        // margin-top: 5px;

        // &:first-child {
        //   margin-top: 0;
        // }
      }
    }
  }
}

.sidebar {
  background: $color-181818;
  height: 100%;

  @include tablet {
    display: flex;
    flex-wrap: wrap;
  }

  &__img {
    display: flex;

    @include tablet {
      width: 50%;
    }

    @include tablet_sm {
      width: 100%;
    }

    img {
      width: 100%;
      height: auto;

      @include tablet {
        object-fit: cover;
        object-position: right;
      }
    }
  }

  &__content {
    padding: 50px 30px;
    color: $color-7E8791;

    @include tablet {
      width: 50%;
    }

    @include tablet_sm {
      width: 100%;
      padding: 40px 20px;
    }

    .title {
      color: $color-FFFFFF;

      &_sm {
        margin-bottom: 10px;

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  &__item {
    margin-bottom: 50px;

    @include tablet_sm {
      margin-bottom: 30px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    &:first-child {
      > ul {
        li {
          padding-left: 0 !important;

          &::before {
            display: none;
          }

          color: $color-FFFFFF;
        }
      }
    }

    &:last-child {
      margin-bottom: 0;
    }

    > ul {
      li {
        position: relative;
        padding-left: 11px;
        margin-bottom: 5px;

        &:last-child {
          margin-bottom: 0;
        }

        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          bottom: 0;
          margin: auto;
          width: 4px;
          height: 4px;
          border-radius: 50%;
          background: $color-BEAD8E;
        }

        a {
          color: $color-7E8791;
        }

        ul {
          margin-bottom: 20px;
        }
      }
    }
  }

  &__list-wrapper {
    padding-left: 0 !important;

    &::before {
      display: none;
    }
  }
}

.language-nav {
  position: absolute;
  top: 20px;
  right: 30px;

  @include tablet {
    right: 20px;
  }

  ul {
    display: flex;

    li {
      font-weight: 450;
      font-size: 14px;
      margin-right: 15px;

      @include tablet {
        font-size: 20px;
        margin-right: 20px;

        &:last-child {
          margin-right: 0;
        }
      }

      &:last-child {
        margin-right: 0;
      }

      a {
        color: $color-181818;
      }
    }
  }
}

